import React, { useState } from 'react'
import SimpleTabs from '../../components/tabs/Mytabs'
import MyTable from '../../components/tables/MyTable'
import makeData from '../../components/tables/makeData'
import { usePopup } from '../../hooks/usePopup';
import { useConfirmation } from '../../hooks/useConfirmation';
import { DeleteButton, EditButton, ViewButton } from '../../components/buttons/AddButton'
import FieldsForm from './FieldsForm';
import { useFieldsService } from './FieldsService';

export default ()=> {
    const label1='List'
    const label2='Add Fields'
    const MyForm=FieldsForm
    const [mydata,actions]=useFieldsService()
    const {MyPopup,openDialog}=usePopup('Edit Fields');
    const {ConfirmPopup,openConfirm}=useConfirmation('Are you sure','once deleted not recoverd');
    const [value,setvalue]=useState({})
    const addorEdit=(v)=>{
    if(Object.keys(value).length>0){actions?.update(v); openDialog()}
    
    else actions?.add(v)
      
      setvalue({})
    }
    const data=React.useMemo(()=>{
        return mydata;
    },[mydata])
    const columns = React.useMemo(
        () => [
         
              {
                Header: "First Name",
                accessor: "firstName"
              },
              {
                Header: "Last Name",
                accessor: "lastName"
          },
         
              {
                Header: "Actions",
                Cell: ({ row }) => (
                  <>
                  <ViewButton
                     
                     onClick={()=>{openDialog()}}
                    />
                     
                    <EditButton
                     
                     onClick={()=>{setvalue(row.original);openDialog()}}
                    />
                     
                    <DeleteButton 
                      onClick={() => openConfirm(()=>actions?.deletec(row.original))}
                    />
                  </>
                )
              }
            ]
          ,
        []
      );
    
    return (<>
        <SimpleTabs label1={label1} label2={label2} form={<MyForm value={value} addorEdit={addorEdit}/>} table={<MyTable columns={columns} data={data}/>}/>
        <MyPopup  maxWidth='sm' fullWidth><MyForm value={value} addorEdit={addorEdit} /></MyPopup>
        <ConfirmPopup/>
        </>
    )
}
