import React, { useReducer } from 'react'
import useLocalStorage from '../../hooks/useLocalStorage';

export const useUserService=()=>{
    const [data,setData]=useLocalStorage('usersList',[]);

    const add=(v)=>{setData([...(data||[]),{id:Date.now(),...v}])}

    const update=(v)=>setData(data=>data.map(item1=>item1.id==v.id?v:item1))
    
    const deletec=(v)=>setData(data=>data.filter(item1=>item1.id!=v.id))
 const actions={add,update,deletec}
return [data,actions]
}