import React from 'react';
import { Route, Switch } from 'react-router-dom';

import DashboardLayout from '../components/layout/dashboard';
import FieldsPage from './fields/FieldsPage';
import ItemPage from './items/ItemPage';
import ProjectsPage from './projects/ProjectsPage';
import SuppliersPage from './suppliers/SuppliersPage';
import UserPage from './users/UserPage';
const baseroot='/dashboard'
const roles=['admin','siteengineer']
export default function Dashboard() {
 
  const menus={
    admin:{
      title:'Admin',
      submenu:[{
        title:'Manage Item',
        link:baseroot+'/items'
      },
      {
        title:'Manage Project',
        link:baseroot+'/projects'
      },
      {
        title:'Manage Supplier',
        link:baseroot+'/suppliers'
      },
      {
        title:'Manage Fields',
        link:baseroot+'/fields'
      },
      {
        title:'Manage Usera',
        link:baseroot+'/users'
      }
    ]
    },
    siteengineer:{
      title:'Site Engineer'
    },
    purchase:{
      title:'Purchase Module'
    },
    store:{
      title:'Store Keeper'
    },
    quotation:{
      title:'Quotation'
    },
    account:{
      title:'Account'
    }

  }







  return (
    
      <DashboardLayout menus={roles.map(item=>menus[item])}>
        <Switch>
         {routes['admin'].map(item=><Route {...item}/>)}
          </Switch>
        </DashboardLayout>
    
  );
}

const routes={
  admin:[{
    path:baseroot+'/items',
    component:ItemPage
  },
  {
    path:baseroot+'/fields',
    component:FieldsPage
  },
  {
    path:baseroot+'/projects',
    component:ProjectsPage
  },
  {
    path:baseroot+'/suppliers',
    component:SuppliersPage
  },
  {
    path:baseroot+'/users',
    component:UserPage
  }
]
}