
import React from 'react'
import { useForm } from '../../hooks/useForm'
import {controls} from '../../components/Inputs'
import { Box, Button } from '@material-ui/core'

export default ({value,addorEdit}) =>{
    const initialValues={firstName:'',lastName:''}
const {values,handleInputChange,resetForm}=useForm(( Object.keys(value).length==0)?initialValues:value,false,{})


     const handleSubmit=(e)=>{
         e.preventDefault();
         addorEdit(values)
        resetForm({});
     }
    return (
        <form onSubmit={handleSubmit}>
            <controls.MyInput1 label='item code' name={'itemCode'}  value={values?.itemCode} onChange={handleInputChange}/>
            <controls.MyInput1 label='itemName' name={'itemName'}  value={values?.itemName} onChange={handleInputChange}/>
            <controls.MyInput1 label='uom' name={'uom'}  value={values?.uom} onChange={handleInputChange}/>
            <controls.MyInput1 label='hsncode' name={'hsncode'}  value={values?.hsncode} onChange={handleInputChange}/>
            <controls.MyInput1 label='itemtype' name={'itemtype'}  value={values?.itemtype} onChange={handleInputChange}/>
            <controls.MyInput1 multiline rows={4} label='specifications' name={'specifications'}  value={values?.specifications} onChange={handleInputChange}/>

<Box textAlign='center'><controls.Submit text={'submit'}/> </Box>
        </form>
    )
}

