import React from 'react'
import Drawer from '@material-ui/core/Drawer';

import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import { Collapse } from '@material-ui/core';
import { NavLink } from 'react-router-dom';
import { ExpandLess, ExpandMore, StarBorder } from '@material-ui/icons';
export default function Sidebar({classes,open,menus }) {
    return (
        <Drawer
        className={classes.drawer}
        variant="persistent"
        anchor="left"
        open={open}
        classes={{
          paper: classes.drawerPaper,
        }}
      >
        <div className={classes.drawerHeader}>
          
        </div>
        <Divider />
        <List>
        {menus?.map(item =>
          item?.submenu ? (
            <SubMenu classes={classes} menu={item} />
          ) : (
            <ListItem button divider component={NavLink} to={item?.link||'/'} activeClassName={classes.active} exact>
              {" "}
              <ListItemIcon>
                
              </ListItemIcon>
              <ListItemText primary={item?.title} />
            </ListItem>
          )
        )}
        </List>
      </Drawer>
    )
}
const SubMenu = ({ classes, menu }) => {
  const [open, setOpen] = React.useState(false);

  const handleClick = () => {
    setOpen(!open);
  };
  return (
    <>
      <ListItem button onClick={handleClick} divider>
        <ListItemIcon>
         
        </ListItemIcon>
        <ListItemText primary={menu?.title} />
        {open ? <ExpandLess /> : <ExpandMore />}
      </ListItem>
      <Collapse in={open} timeout="auto" unmountOnExit>
        <List component="div" disablePadding >
          {menu?.submenu?.map(({ title,link }) => (
            <ListItem button divider className={classes.nested} exact component={NavLink} to={link||'/'} activeClassName={classes.active}>
              <ListItemIcon>
                <StarBorder />
              </ListItemIcon>
              <ListItemText primary={title} />
            </ListItem>
          ))}
        </List>
      </Collapse>
    </>
  );
};
