import React from 'react'
import {Add as AddIcon,Edit as EditIcon,Delete as DeleteIcon,Visibility as ViewIcon} from '@material-ui/icons';
import Button from '@material-ui/core/Button';
import { IconButton } from '@material-ui/core';

export function AddButton({children,...others}) {
    return (
        <Button
                  size="small"
                  variant="outlined"
                  color="primary"
                 startIcon={<AddIcon />}
                 {...others}
                >
                  {children}
                </Button>
    )
}
export function EditButton({children,...others}) {
    return (
        <IconButton
                  size="small"
                  style={{color:'yellow'}}
                 
                 {...others}
                 
                >
               <EditIcon />
                </IconButton>
    )
}

export function DeleteButton({children,...others}) {
    return (
        <IconButton
                  size="small"
                  style={{color:'red'}}
                 {...others}
                >
             <DeleteIcon />
                </IconButton>
    )
}
export function ViewButton({children,...others}) {
    return (
        <IconButton
                  size="small"
                  style={{color:'skyblue'}}
                 {...others}
                >
             <ViewIcon />
                </IconButton>
    )
}

