
import React from 'react'
import { useForm } from '../../hooks/useForm'
import {controls} from '../../components/Inputs'
import { Box, Button } from '@material-ui/core'

export default ({value,addorEdit}) =>{
    const initialValues={firstName:'',lastName:''}
const {values,handleInputChange,resetForm}=useForm(( Object.keys(value).length==0)?initialValues:value,false,{})


     const handleSubmit=(e)=>{
         e.preventDefault();
         addorEdit(values)
        resetForm();
     }
    return (
        <form onSubmit={handleSubmit}>
            <controls.MyInput1 label='projectName' name={'projectName'}  value={values?.projectName} onChange={handleInputChange}/>
            <controls.MyInput1 multiline rows={4} label='description' name={'description'}  value={values?.description} onChange={handleInputChange}/>
<Box textAlign='center'><controls.Submit text={'submit'}/> </Box>
        </form>
    )
}

