import { Box, Button, TextField, Typography } from '@material-ui/core'
import React from 'react'

export function MyInput({label,...other}) {
    return (
        <Box mb={1}>
            <Typography gutterBottom variant='subtitle2'>{label}</Typography>
            <TextField size='small' variant='outlined' color='primary' fullWidth {...other}/>
        </Box>
    )
}

export  function MyInput1({label,...other}) {
    return (
        <Box mb={1} display='flex' alignItems='center'>
            <Typography variant='subtitle2' style={{margin:'0 20px',textAlign:'right',minWidth:'100px'}}>{label+":"}</Typography>
            <TextField size='small' variant='outlined' color='primary' fullWidth {...other}/>
        </Box>
    )
}

export  function Submit({text,...other}) {
    return (
       <Button type='submit' variant='contained' color='primary' {...other}>{text}</Button>
    )
}